body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --mainColor: #3f1508;
  --secondColor: #ffffff;
  --mainColorBackground: #a1e668;
  --mainColorDark: #bb3737;
  --mainColorLight: #f3b43e;
  --darkGrayColor: rgb(128, 128, 128);
}
/* bootstrap overrides fonts etc so use !important */
body {
  font-family: "Roboto", sans-serif !important;
  background: #a1e668 !important;
  background: var(--mainColorBackground) !important;
  color: #bb3737 !important;
  color: var(--mainColorDark) !important;
}
/* faking background opacity */
#background-div {
  position: relative;
}
#cart-div {
  background: #ffffff;
  background: var(--secondColor);
}
#background-div::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: url(/REACT-e-commerce-shop-local-products/static/media/fields1.0560fb16.jpg);
  background-size: cover;
  opacity: 0.4;
  z-index: -1;
}

.text-title {
  font-family: "Anton", sans-serif;
  letter-spacing: 0.3rem;
  text-transform: uppercase;
}
.text-color {
  color: #3f1508;
  color: var(--mainColor);
}
.text-light {
  color: #ffffff;
  color: var(--secondColor);
}
.text-super-light {
  color: rgb(128, 128, 128);
  color: var(--darkGrayColor);
  font-size: 0.8rem;
  font-style: italic;
}
.btn-black {
  background: transparent;
  text-transform: capitalize;
  font-size: 1.3rem !important;
  color: #bb3737;
  color: var(--mainColorDark);
  border: 2px solid #bb3737 !important;
  border: 2px solid var(--mainColorDark) !important;
  border-radius: 8px !important;
  padding: 0 0.6rem !important;
}
.btn-black:hover {
  background: #bb3737 !important;
  background: var(--mainColorDark) !important;
  color: #f3b43e !important;
  color: var(--mainColorLight) !important;
}

.cart-icon {
  cursor: pointer;
  font-size: 1.2rem;
  color: #3f1508;
  color: var(--mainColor);
}
.cart-icon:hover {
  color: #bb3737;
  color: var(--mainColorDark);
}

.promotion-container {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 100%;
}

.promotion-animation {
  -webkit-animation: promotion 6s infinite cubic-bezier(0.165, 0.84, 0.44, 1);
          animation: promotion 6s infinite cubic-bezier(0.165, 0.84, 0.44, 1);
}

@-webkit-keyframes promotion {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }

  50% {
    -webkit-transform: translateX(60%);
            transform: translateX(60%);
  }

  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes promotion {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }

  50% {
    -webkit-transform: translateX(60%);
            transform: translateX(60%);
  }

  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

